@import url(http://fonts.googleapis.com/css?family=Lato:700);
body {
  margin: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  margin: 0;
}

.App {
  width: 94%;
  max-width: 30rem;
  margin: 0 auto;
  padding-top: 1rem;
}

/* button style */
.Button {
  color: #28aadc;
  background-color: #fff;
  border: 2px solid #28aadc;
  border-radius: 0;
  font-weight: 700;
  width: 100%;
  margin-bottom: 0.2rem;
  height: 3rem;
  text-align: center;
  padding-top: 0rem;
  /* padding-bottom: 0.7rem; */
  line-height: 1;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
}

.Button:hover {
  background-color: #28aadc;
  color: #fff;
}

.Button:focus {
  outline: 0;
}

#myCanvas {
  width: 100%;
}

.Checkbox {
  position: relative;
  width: 100%;
  margin-bottom: 0.2rem;
  text-align: center;
}
.Checkbox__checkbox {
  display: none;
}
.Checkbox__label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid #FFFFF;
  border-radius: 0px;
}
.Checkbox__inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  -webkit-transition: margin 0.3s ease-in 0s;
  transition: margin 0.3s ease-in 0s;
}
.Checkbox__inner:before,
.Checkbox__inner:after {
  display: block;
  float: left;
  width: 50%;
  height: 3rem;
  padding: 0;
  line-height: 44px;
  font-size: inherit;
  color: white;
  font-family: inherit;
  box-sizing: border-box;
}
.Checkbox__inner:before {
  content: "Typo ON";
  /* padding-left: 21px;*/
  background-color: #28aadc;
  color: #ffffff;
}
.Checkbox__inner:after {
  content: "Typo OFF";
  /* padding-right: 21px; */
  background-color: #eeeeee;
  color: #999999;
  /* text-align: right; */
}
.Checkbox__switch {
  display: block;
  width: 2rem;
  height: 2rem;
  background: #ffffff;
  position: absolute;
  top: 0.5rem;
  bottom: 0;
  right: calc(100% - 2rem - 0.5rem);
  border: 2px solid #FFFFF;
  border-radius: 0px;
  -webkit-transition: right 0.3s ease-in 0s;
  transition: right 0.3s ease-in 0s;
}
.Checkbox__checkbox:checked + .Checkbox__label .Checkbox__inner {
  margin-left: 0;
}
.Checkbox__checkbox:checked + .Checkbox__label .Checkbox__switch {
  right: 0.5rem;
}

